.btn {
    text-decoration: none;
    border: none;
    
    min-width: 50px;
    min-height: 25px;
    border-radius: 5px;
    
    font-weight: bold;
    font-size: 12px;
    

    &-sync {

        border: solid 1px;
        border-color: $primary;
        
        background: transparent;
        color: $text-color;

        margin-top: 5px;
        

        &:hover {
            cursor: pointer;
            background: #f2f2f2;

            .sync {
                animation: load 1s;
            }
    
        }
    }
}